/* レイアウト全体の設定 */
.layout {
  display: flex;
  min-height: 100vh;
  background-color: #000000;  /* 背景色を黒に */
  color: #ffffff;  /* テキストを白に */
}

/* 左側のサイドバー */
.sidebar-left {
  width: 200px;
  background-color: #000000;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  overflow-y: auto;
  border-right: 1px solid #444;
}

/* 中央のメインコンテンツ */
.container-main {
  margin-left: 220px;
  margin-right: 250px;
  margin-top: 0px;  /* 上側の余白を完全に削除 */
  padding-top: 0px; /* 必要に応じてパディングも削除 */
  flex-grow: 1;
  background-color: #000000;  /* 黒背景 */
  color: #ffffff;
}

/* 右側のナビゲーションバー */
.sidebar-right {
  width: 250px;
  background-color: #000000;
  padding: 20px;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  overflow-y: auto;
  border-left: 1px solid #444;
}

/* ボタンとリンクのカラー */
a {
  color: #1e90ff;
}

a:hover {
  text-decoration: underline;
  color: #87cefa;
}

button.btn-primary {
  background-color: #1e90ff;
  border-color: #1e90ff;
  color: #ffffff;
}

button.btn-primary:hover {
  background-color: #4682b4;
  border-color: #4682b4;
}

.alert-primary {
  background-color: #1e90ff;
  color: #ffffff;
  border-color: #4682b4;
}

/* カードのデザイン（メッセージ表示） */
.card {
  background-color: #2c2c2c;  /* 暗い灰色に変更 */
  border: 1px solid #444;
  border-radius: 8px;
}

.card-body {
  padding: 15px;
  color: #505050;
}

/* ページネーションのデザイン */
.pagination .page-link {
  color: #12569af6;
}

.pagination .page-link:hover {
  background-color: #444;
}

/* 全体的なテキストのスタイル */
h1, h2 {
  color: #ffffff;
}

p {
  color: #ffffff;
}

/* bodyの余白リセット */
body {
  margin: 0;
  padding: 0;
}
